@import 'src/scss/global.scss';

.DrawerToggle {
  width: 40px;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-items: center;
  padding: 10px 0;
  box-sizing: border-box;
  cursor: pointer;
  margin-right:20px;

  div {
    width: 90%;
    height: 3px;
    background-color: black;
  }
}

@media (min-width: $drawerLimit) {
  .HideOnDesktop{
    display: none;
  }
}